import React, { Component } from 'react';
import './change_password.scss';
import { UI } from '../../components';

class ChangePassword extends Component {
  render() {
    return (
      <div className="change_password">
        <div className="title_change_password">
          <div className="line_title">
            <span>i mt khu</span>
          </div>
          <hr className="line-header" />
        </div>
        <div className="box_chage_pass">
          <UI.Formik
            initialValues={{ oldPass: '', newPass: '', reNewPass: '' }}
            validationSchema={UI.yup.object().shape({
              oldPass: UI.yup.string().required('not null'),
              newPass: UI.yup.string().required('not null'),
              reNewPass: UI.yup.string().required('not null')
            })}
            onSubmit={this.onHandleSubmit}
            render={props => (
              <form onSubmit={props.handleSubmit}>
                <div className="box-input">
                  <div className="title-product-code">Mt khu c</div>
                  <div className="card-cover">
                    <UI.Field
                      {...props}
                      name="oldPass"
                      type="oldPass"
                      placeholder="Mt khu c"
                      value={props.values.oldPass}
                      onChange={props.handleChange}
                      autoComplete="off"
                    />
                  </div>
                </div>
                <div className="box-input">
                  <div className="title-product-code">Mt khu mi</div>
                  <div className="card-cover">
                    <UI.Field
                      {...props}
                      name="newPass"
                      type="newPass"
                      placeholder="Mt khu mi"
                      value={props.values.newPass}
                      onChange={props.handleChange}
                      autoComplete="off"
                    />
                  </div>
                </div>
                <div className="box-input">
                  <div className="title-product-code">Nhp li mt khu mi</div>
                  <div className="card-cover">
                    <UI.Field
                      {...props}
                      name="reNewPass"
                      type="reNewPass"
                      placeholder="Nhp li mt khu mi"
                      value={props.values.reNewPass}
                      onChange={props.handleChange}
                      autoComplete="off"
                    />
                  </div>
                </div>
                <div className="group-btn-pop-up">
                  <UI.Button type="submit" name="Lu" className="btn-pop-up" />
                  <UI.Button type="button" name="Hu" className="btn-pop-up" />
                </div>
              </form>
            )}
          />
        </div>
      </div>
    );
  }
}
export default ChangePassword;
